.card {
  background: #fff;
  border-radius: 2px;
  height: 20%;
  margin: 1rem;
  width: 20%;
  box-shadow: 0 3px 6px #999, 0 3px 6px #999;
}

.card > .img-container {
  height: 100%;
  width: auto;
  overflow: hidden;
  text-align: center;
  background:rgb(49, 48, 48);
}

.card > .img-container > img {
  max-width: 100%;
  max-height: 100%;
  height: auto;
  width: auto;
}

.card > .img-container > img:before {
  transition: 500ms all linear;
}

.card > .img-container > img:hover {
  -webkit-transform: scale(1.1);
  transform: scale(1.1);
}

.card > .img-container > img:hover:before {
  opacity: 1;
}


.wrapper {
  /* background: #ECEFF1; */
  background-color: white;
  height: 100%;
  width: 100%;
  display: flex;
  flex-flow: row wrap;
  padding: 20px;
  justify-content: space-around;
  align-content: flex-start;
  overflow: auto;
}

.jumbotron {
  min-height: 80px;
  padding-top: 40px;
}

#page-header {
  height: 200px;
  background: url(/clicky/static/media/image4.f3115785.jpeg);
  background-position: 0 -3995px;
  background-attachment: fixed;
  color: #fff;
  border-bottom: 1px #eee solid;
  padding-top: 50px;
}


.sticky {
  width: 100%;
  text-align: center;
  font-size: 1.5rem;
  text-decoration: none;
  background-color: #f4f4f4;
  color: black;
  padding: 0px;

  position:fixed;    /*Here's what sticks it*/
  bottom:0;          /*to the bottom */
  left:0;            /*and left of the body.*/
}
.title {
  width: 100%;
  text-align: center;
  margin-bottom: 50px;
}

* {
  box-sizing: border-box;
  font-family: sans-serif;
}

html, body, #root {
  padding: 0;
  margin: 0;
  height: 100%;
}

